html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%; }

body {
  overflow-x: hidden !important;
  font-family: 'Open Sans', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333333;
  background: #333333; }
  body * {
    box-sizing: border-box; }
    body *:focus {
      outline-color: #e59700; }

h1, h2, h3, h4, button, input, select, ul, li, table, th, td {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  text-align: center; }

a, input, button, select {
  color: #333333; }

a {
